@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*===== VARIABLES CSS =====*/
:root{
  /*===== Colores =====*/
  --first-color: #1A73E8;
  --input-color: #80868B;
  --border-color: #DADCE0;

  /*===== Fuente y tipografia =====*/
  --body-font: 'Roboto', sans-serif;
  --normal-font-size: 1rem;
  --small-font-size: .75rem;
}
  
/*===== BASE =====*/
*,::before,::after{
  box-sizing: border-box;
}
body{
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-family: var(--body-font);
  font-size: 1rem;
  font-size: var(--normal-font-size);
}
h1{
  margin: 0;
}

/*===== FORM =====*/
.appStyles_one_form__1qyCJ{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  height: 60vh;
}
.appStyles_form__3kjLU{
  width: 460px;
  padding: 4rem 2rem;
  border-radius: 1rem;
  box-shadow: 0 10px 25px rgba(92,99,105,.2);
}
.appStyles_form_title__md-3F{
  font-weight: 500;
  margin-bottom: 3rem;
  color: rgb(200,100,100);
}
.appStyles_form_div__2vDaY{
  position: relative;
  height: 48px;
  margin-bottom: 1.5rem;
}
.appStyles_form_input__13Ngp{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-size: 1rem;
  font-size: var(--normal-font-size);
  border: 1px solid #DADCE0;
  border: 1px solid var(--border-color);
  border-radius: .5rem;
  outline: none;
  padding: 1rem;
  background: none;
  z-index: 1;
}
.appStyles_form_label__30pkB{
  position: absolute;
  left: 1rem;
  top: 1rem;
  padding: 0 .25rem;
  background-color: #fff;
  color: #80868B;
  color: var(--input-color);
  font-size: 1rem;
  font-size: var(--normal-font-size);
  transition: .3s;
}
.appStyles_form_button__2OOxw{
  display: block;
  margin-left: auto;
  padding: .75rem 2rem;
  outline: none;
  border: none;
  background-color: #1A73E8;
  background-color: var(--first-color);
  color: #fff;
  font-size: 1rem;
  font-size: var(--normal-font-size);
  border-radius: .5rem;
  cursor: pointer;
  transition: .3s;
}
.appStyles_form_button__2OOxw:hover{
  box-shadow: 0 10px 36px rgba(20,100,0,.5);
}

.appStyles_form__3kjLU:hover{
  box-shadow: 0 2px 36px rgba(0,0,.1,.25); 
}

/*Input focus move up label*/
.appStyles_form_input__13Ngp:focus + .appStyles_form_label__30pkB{
  top: -.5rem;
  left: .8rem;
  color: #1A73E8;
  color: var(--first-color);
  font-size: .75rem;
  font-size: var(--small-font-size);
  font-weight: 500;
  z-index: 10;
}


/*Input focus sticky top label*/
.appStyles_form_input__13Ngp:not(:-ms-input-placeholder).appStyles_form_input__13Ngp:not(:focus)+ .appStyles_form_label__30pkB{
  top: -.5rem;
  left: .8rem;
  font-size: .75rem;
  font-size: var(--small-font-size);
  font-weight: 500;
  z-index: 10;
}
.appStyles_form_input__13Ngp:not(:placeholder-shown).appStyles_form_input__13Ngp:not(:focus)+ .appStyles_form_label__30pkB{
  top: -.5rem;
  left: .8rem;
  font-size: .75rem;
  font-size: var(--small-font-size);
  font-weight: 500;
  z-index: 10;
}

/*Input focus*/
.appStyles_form_input__13Ngp:focus{
  border: 1.5px solid #1A73E8;
  border: 1.5px solid var(--first-color);
}

